import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '../../components/common/upload-images.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue';
export default {
	// 页面渲染完
	onReady() {
		this.openPopup();
	},
	// 监听返回
	onBackPress () {
		// 如果有值
		if (!this.text && this.imageList.length < 1) {
			return;
		}
		if (!this.isAsk) {
			this.saveDraft();
			return true;
		}
	},
	data() {
		return {
			changeLookList: ['所有人可见', '仅自己可见'],
			yinsi: '所有人可见',
			text: '', // 多行文本内容
			imageList: [], // 选中的图片列表
			isAsk: false
			
		}
	},
	components:{
		uniNavBar,
		uploadImages,
		uniPopup
	},
	methods: {
		// 返回
		back () {
			// 返回上一层
			uni.navigateBack({
				delta: 1
			});
		},
		// 发布
		submit () {
			
		},
		// 隐私 
		changeLook () {
			// 官网-->接口-->界面-->交互反馈
			uni.showActionSheet({
				// itemList可选项列表， itemList接收一个数组
				itemList: this.changeLookList, 
				success: res => {
					// res.tapIndex 选择的列表的下标
					this.yinsi = this.changeLookList[res.tapIndex]
				}
			});
		},
		// 获取图片列表
		getImageList (imgList) {
			this.imageList = imgList;
		},
		change(e) {
			// console.log('是否打开:' + e.show)
		},
		// 打开弹出公告
		openPopup () {
			this.$nextTick(() => {
				this.$refs['showpopup'].open();
			})
		},
		// 关闭公告
		closePopup () {
			this.$nextTick(() => {
				this.$refs['showpopup'].close();
			})
		},
		// 保存草稿
		saveDraft () {
			uni.showModal({
				content: '是否要保存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm) { // 点击保存
						console.log('保存');
					} else {
						console.log('不保存');
					}
					this.isAsk = true;
					this.back();
				}
			});
		}
	}
}
